import React from 'react';
import { graphql, HeadProps, PageProps } from 'gatsby';
import BooksPage from 'containers/BooksPage';
import SEO from 'components/SEO';
import { getAlternateLanguages } from 'utils/i18nPath';
import type { SupportedLanguages } from 'models/languages';

const BooksTemplate = (props: PageProps<Queries.BooksPageQuery, { lang: string }>) => {
  return <BooksPage {...props} />;
};

export default BooksTemplate;

export const Head = ({ pageContext, location }: HeadProps<Queries.BooksPageQuery, { lang: string }>) => {
  const alternateLanguages = getAlternateLanguages(pageContext.lang as SupportedLanguages, '/books/');

  return (
    <SEO
      title="Books"
      description="시리즈로 엮인 글 모음"
      lang={pageContext.lang}
      pathname={location.pathname}
      alternateLanguages={alternateLanguages}
      includeXDefaultAlternate={true}
    />
  );
};

export const pageQuery = graphql`
  query BooksPage($lang: String!) {
    allMarkdownRemark(
      filter: { fields: { mdType: { eq: "books" }, lang: { eq: $lang } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      edges {
        node {
          ...Book
        }
      }
    }
  }
`;
